@mixin mob320 {
  @media (max-width: 320px) {
    @content;
  }
}
@mixin mob375 {
  @media (max-width: 375px) {
    @content;
  }
}
@mixin mob425 {
  @media (max-width: 425px) {
    @content;
  }
}
@mixin mob445 {
  @media (max-width: 445px) {
    @content;
  }
}
@mixin mob480 {
  @media (max-width: 480px) {
    @content;
  }
}
@mixin mob600 {
  @media (max-width: 600px) {
    @content;
  }
}

@mixin Xs {
  @media (max-width: 767px) {
    @content;
  }
}
//For Small
@mixin Sm {
  @media (max-width: 991px) {
    @content;
  }
}
//For Medium
@mixin Md {
  @media (min-width: 992px) {
    @content;
  }
}
//For Extra Large
@mixin Lg {
  @media (min-width: 1200px) {
    @content;
  }
}

//For medium Large
@mixin kh {
  @media (min-width: 1200px) and (max-width: 1600px) {
    @content;
  }
}

// English
@mixin LTR {
  html[dir="ltr"] & {
    @content;
  }
}
