body {
  .content-wrapper {
    background-color: $dark;
    background-image: url("../images/bg-pattern.png");
    background-size: cover;
    height: 100vh;
    overflow-y: auto;
    &.active {
      padding-left: 250px;
    }
    display: flex;
    flex-direction: column;

    transition: 0.5s;
    .container{
      padding-top: 7rem;
      padding-bottom: 10rem;
    }
  }
  font-family: "reg", sans-serif;
  @include Sm {
    text-align: center;
  }
}

* {
  font-family: inherit;
}

input,
a,
button {
  font-family: "reg", sans-serif;
  text-decoration: none;
  &:focus {
    outline: none;
    text-decoration: none;
  }

  &:hover {
    text-decoration: none;
  }
}

button {
  cursor: pointer;

  &:focus,
  &:active {
    outline: none;
    box-shadow: none;
  }
}

ul.res {
  padding: 0;
  margin: 0;
  list-style: none;
}
