.dash-navbar-top {
  margin-bottom: 0;
  min-height: 61px;
  padding-top: 0;
  padding-bottom: 0;
  border-radius: 0;
  line-height: 1;
}
.dash-navbar-top .nb-btn-toggle {
  position: absolute;
  left: 16px;
  top: 13.5px;
  font-size: 21px;
  background-color: rgba(0, 0, 0, 0);
  border-width: 0;
  color: #d3d1dd;
  outline: 0;
}


.dash-navbar-top .navbar-toggle {
  border-width: 0;
  font-size: 24px;
  color: #d3d1dd;
  padding: 0 10px;
  margin-top: 14px;
  margin-right: 15px;
}
.dash-navbar-top .navbar-toggle:hover,
.dash-navbar-top .navbar-toggle:focus {
  background-color: transpardnt;
}

.dash-navbar-left {
  position: fixed;
  background-color: #2a2d35;
  width: 250px;
  top: 0;
  bottom: 0;
  left: -250px;
  overflow-y: auto;
  z-index: 999;
  padding-top: 5px;
  transition: 0.5s;

  &.active {
    left: 0px;
  }
}
.navbar-brand {
  display: flex;
  background: $min;
  width: 100%;
  color: #fff;
  text-transform: uppercase;
  padding: 0 20px;
  margin: -10px 0 30px 0;
  height: 85px;
  line-height: 65px;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  // padding: 10px;
  img {
    width: 150px;
    height: 80px;
  }
}
.navbar-brand:hover {
  color: #fff;
}
.navbar .form-inline {
  margin-top: 12px;
}
.navbar .btn {
  background: $min;
  color: #fff;
}
@media (max-width: 767px) {

  .dash-navbar-left.nb-visible.nb-show {
    left: 0;
  }
}
.dash-navbar-left.nb-hidden {
  left: -250px;
}
.dash-navbar-left.nb-hidden.nb-show {
  left: 0;
}
.dash-navbar-left .nb-nav-title {
  text-transform: uppercase;
  color: #7f799c;
  border-bottom: 1px solid rgba(255, 152, 0, 0.1);
  padding: 10px 25px;
  font-size: 11px;
  letter-spacing: 1px;
}
.dash-navbar-left .nb-nav {
  padding-left: 0;
  list-style: none;
}
.dash-navbar-left .nb-nav ul {
  padding-left: 0;
  list-style: none;
}
.dash-navbar-left .nb-nav li {
  position: relative;
}
.dash-navbar-left .nb-nav li a {
  display: block;
  outline: 0;
  padding: 8px 25px 7px;
  font-size: 13px;
  color: #b7b4c7;
}
.dash-navbar-left .nb-nav li a:hover,
.dash-navbar-left .nb-nav li a:focus {
  color: #eeeeee;
  text-decoration: none;
  background-color: rgba(255, 152, 0, 0.1);
  -webkit-box-shadow: inset 3px 0 0 $min;
  box-shadow: inset 3px 0 0 $min;
}
.dash-navbar-left .nb-nav li a:hover .badge,
.dash-navbar-left .nb-nav li a:focus .badge {
  color: #eee;
}
.dash-navbar-left .nb-nav li a > .nb-link-icon {
  padding-right: 8px;
}
.dash-navbar-left .nb-nav li a > .nb-btn-sub-collapse {
  position: absolute;
  right: 25px;
  top: 10px;
}
.dash-navbar-left .nb-nav li a > .badge {
  position: absolute;
  right: 24px;
  top: 6px;
  padding: 3px 10px;
  color: #a7a9ac;
  background-color: transparent;
  border: 1px solid #4f4a65;
}
.dash-navbar-left .nb-nav li a.collapsed .nb-btn-sub-collapse {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  transform: rotate(180deg);
}
.dash-navbar-left .nb-nav li.active > a {
  background-color: rgba(255, 152, 0, 0.1);
  -webkit-box-shadow: inset 3px 0 0 $min;
  box-shadow: inset 3px 0 0 $min;
}
.dash-navbar-left .nb-nav li .nb-sub-one {
  background-color: #15141b;
}
.dash-navbar-left .nb-nav li .nb-sub-one li {
  border-top: 1px solid #1c1b24;
}
.dash-navbar-left .nb-nav li .nb-sub-one li a {
  display: block;
  color: #a7a9ac;
}
.dash-navbar-left .nb-nav li .nb-sub-one li a:hover,
.dash-navbar-left .nb-nav li .nb-sub-one li a:focus {
  color: #eeeeee;
  background-color: #1c1b24;
  -webkit-box-shadow: inset 3px 0 0 #1c1b24;
  box-shadow: inset 3px 0 0 #1c1b24;
}
.dash-navbar-left .nb-nav li .nb-sub-one li a > .nb-link-icon {
  padding-right: 8px;
}
.dash-navbar-left .nb-nav li .nb-sub-one li a > .badge {
  color: #a7a9ac;
  background-color: transparent;
  border: 1px solid #211f2a;
}
.dash-navbar-left .nb-nav li .nb-sub-one li.active > a {
  background-color: #1c1b24;
  -webkit-box-shadow: inset 3px 0 0 #1c1b24;
  box-shadow: inset 3px 0 0 #1c1b24;
}
.dash-navbar-left .nb-nav li .nb-sub-two {
  background-color: #211f2a;
}
.dash-navbar-left .nb-nav li .nb-sub-two li {
  border-top-color: 1px solid #282533;
}
.dash-navbar-left .nb-nav li .nb-sub-two li a {
  color: #a7a9ac;
}
.dash-navbar-left .nb-nav li .nb-sub-two li a:hover,
.dash-navbar-left .nb-nav li .nb-sub-two li a:focus {
  color: #eeeeee;
  background-color: #282533;
  -webkit-box-shadow: inset 3px 0 0 #282533;
  box-shadow: inset 3px 0 0 #282533;
}
.dash-navbar-left .nb-nav li .nb-sub-two li a > .badge {
  color: #a7a9ac;
  background-color: transparent;
  border: 1px solid #211f2a;
}
.dash-navbar-left .nb-nav li .nb-sub-two li.active > a {
  background-color: #1c1b24;
  -webkit-box-shadow: inset 3px 0 0 #1c1b24;
  box-shadow: inset 3px 0 0 #1c1b24;
}
/* Content wrap
/ -------------------------------------------------- */
.content-wrap {
  position: relative;
}
.content-wrap.nb-visible {
  margin-left: 250px;
}
@media (max-width: 767px) {
  .content-wrap.nb-visible {
    margin-left: 0;
    -webkit-transition: all 300ms;
    -o-transition: all 300ms;
    transition: all 300ms;
  }
  .content-wrap.nb-visible.nb-push {
    -webkit-transform: translate(250px, 0);
    -ms-transform: translate(250px, 0);
    -o-transform: translate(250px, 0);
    transform: translate(250px, 0);
  }
}
.content-wrap.nb-hidden {
  margin-left: 0;
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
}
.content-wrap.nb-hidden.nb-push {
  margin-left: 250px;
}
@media (max-width: 767px) {
  .content-wrap.nb-hidden.nb-push {
    margin-left: 0;
    -webkit-transform: translate(250px, 0);
    -ms-transform: translate(250px, 0);
    -o-transform: translate(250px, 0);
    transform: translate(250px, 0);
  }
}
.content-wrap.content-opacity.nb-overlay,
.content-wrap.content-opacity.nb-push {
  opacity: 0.3;
  filter: alpha(opacity=30);
}

.nav-torger {
  position: relative;
  width: 55px;
  height: 55px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 10px;
  border-radius: 50%;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  cursor: pointer;
}
.nav-torger span {
  width: 28px;
  display: block;
  height: 3px;
  background-color: #fff;
  border-radius: 120px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.nav-torger span:not(:last-of-type) {
  margin-bottom: 5px;
}
.nav-torger.active span {
  background-color: #fff;
}
.nav-torger.active span:nth-child(1) {
  -webkit-transform: translateY(8px) rotate(135deg);
  transform: translateY(8px) rotate(135deg);
}
.nav-torger.active span:nth-child(2) {
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg);
  opacity: 0;
}
.nav-torger.active span:nth-child(3) {
  -webkit-transform: translateY(-8px) rotate(-135deg);
  transform: translateY(-8px) rotate(-135deg);
}
.nav-torger.scrolled {
  background-color: rgba(15, 36, 96, 0.988);
  -webkit-transform: scale(1);
  transform: scale(1);
}
.nav-torger.scrolled span {
  background-color: #fff;
}
nav{
  padding: 10px  0;
  background-color: #2a2d35;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transition: .5s;
  &.active{
    padding-left: 250px;
  }
  z-index: 100;
  .nav-conatiner{


    display: flex;
    align-items: center;
    justify-content: space-between;
    .left-box{
      display: flex;
      .info{
        display: flex;
        align-items: center;
        i{
          margin-right: 10px;
          @media (max-width: 480px){
            font-size: 13px
          }
          @media (max-width: 320px){
            display: none;
          }

        }
        p{
          margin-bottom: 0;
          color: #fff;

        }
        a{
          color: #fff;
          padding: 0 13px;
          transition: .5s;
          &:hover{
            color: $min;
          }
          @media (max-width: 480px){
            font-size: 12px
          }

        }
      }
      .user-box{
        display: flex;
        align-items: center;
        position: relative;
        &:hover{
          ul{
            transform: translate(0);
            opacity: 1;
            visibility: visible;
          }
        }
        ul{
          position: absolute;
          top: 50px;
          right: 0;
          background-color: #fff;
          list-style: none;
          padding-inline-start:0;
          border-radius: 15px;
          padding: 5px 0;
          transform: translate(10px , 20px);
          opacity: 0;
          transition: .3s;
          visibility: hidden;
          li{
            a{
              display: flex;
              align-items: center;
              padding: 10px 15px ;
              transition: .5s;
              color: $gray;
              &:hover{
                background-color: rgba(#ccc , .4)
              }
              i{
                margin-right: 10px;
              }
            }
          }
         }
        h4{
          color: #fff;
          font-size: 13px;
          padding: 0 10px;
          margin-bottom: 0;
          @media (max-width: 480px){
            display: none;
          }
        }
        .avatar{
          width: 40px;
          height: 40px;
          img{
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}
.close-sm{
  position: absolute;
  top: 5px;
  right:  10px;
  background-color: transparent;
  color: #fff;
  border: none;
  font-size: 20px;
  display: none;
  @media(max-width: 768px){
    display: block;
  }
}
