@import "main";
// End Of Import

// Start login
.logo-box {
  width: 40%;
  align-self: center;
  margin: auto;
  padding: 20px;
  @media (max-width: 480px){
    width: 60%;
  }
  img{
    width: 100%;
    height: 100%;
  }
}
.ticket-form{
  .col-12{
    margin-top: 20px
  }
}
.login {
  padding: 30px 0;
  background-color: $dark;
  background-image: url("../images/bg-pattern.png");
  background-size: cover;

  form {
    margin: auto;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px 30px 20px;
    @media (max-width: 480px){
      width: unset;
       margin: unset;

    }


    background-color: $darkAlt;
    border-radius: 15px;
    flex-wrap: wrap;
    flex-direction: column;
  }
  .cap-container {
    width: 100%;
    img {
      margin-bottom: 25px;
      display: block;
    }
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .signUp {
    margin-top: 20px;
    font-family: $light;
    color: $gray;
    a {
      transition: 0.5s;
      margin-left: 10px;
      color: $min;
      &:hover {
        color: #fff;
      }
    }
  }
}
.wrapper-conatiner{
  background-color: $sec;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, .2);
  padding: 30px 25px;
  border-radius: 15px;
  color: #ced4da;
  overflow-x: auto;
  &.profile{
    background-color: transparent;
    box-shadow: none;
  }
}
.profile-wrapper{
  background-color: $sec;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, .2);
  padding: 15px;
  border-radius: 15px;
  width: 100%;
  color: #ced4da;
  margin-bottom: 20px;
  overflow-x: auto;

  ul{
    li{
      margin-bottom: 10px;
      &:not(:last-of-type){
        border-bottom: .5px solid $darkAlt;
      }
      padding-bottom: 10px;
      a{
        padding: 10px;
        color: #ced4da;
      }
    }
  }
  form{
    width: 80%;
    margin: auto;
    @media(max-width: 480px){
      width: 100%;
    }
    .group{
      margin-top: 40px;
    }

  }
}
.article-title{
  font-size: 18px;
  padding-bottom: 10px;
  margin-bottom: 30px;
  border-bottom: 1px solid #ced4da;
}
.sub-title{
  font-size: 15px;
  margin-bottom: 15px;
  span{
    margin-left: 10px;
    color: #fff;
    background-color: $min;
    padding: 5px;
    border-radius: 4px;
  }
}
.article-head{
  h3{
    font-size: 17px;
    padding-bottom: 15px;
    margin-bottom: 15px;
    border-bottom: 1px solid #ced4da;
    font-family: $bold
  }
  p{
    font-family: $light;
    margin-bottom: 6px
  }
  h5{
    width: 50%;
    margin-left: auto;
    text-align: right;
    margin-top: 20px;
    font-size: 15px;
  }

}
.cart-list{
  li{
    display: flex;
    align-items: center;
    justify-content: space-between;
    h3{
      font-size: 20px
    }
    h5{
      font-size: 16px;
    }
  }
}
.article {
  ul{
    list-style: none;
    padding-inline-start:0;
    li{
      display: flex;
      margin-bottom: 10px;
      h5{
        margin-right: 10px;
        font-size: 18px
      }
      span{
        font-family: $light
      }
    }
  }
}
.box-container{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #ced4da;
  width:200px;
  padding:25px  ;
  border-radius: 15px;
  background-color: $darkAlt;
  margin: auto;
  cursor: pointer;
  transition: .5s;
  margin-bottom: 20px;
  @media (max-width: 480px){
    padding:17px  ;
}

  &:hover{
    color: $min;
  }
  i{
    margin-bottom: 15px;
    font-size: 20px;
  }
}
footer{
  background-color: #2a2d35;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: auto;
  padding: 20px 0;
  margin-top: auto;
   p{
    color: #fff;
    margin-bottom: 0;
  }
}
.selct-num{
  display: flex;
  margin-top: 25px;
  align-items: center;;
  @media (max-width: 480px){
  flex-direction: column;
  align-items: center;
  justify-content: center;
  button{
    margin: 20px auto 10px;
  }
}

    .right{
      display: flex;
      align-items: center;
      h3{
        font-size: 17px;
        margin-bottom: 0;
      }
      select{
        margin: 0 15px;
      }
    }
    button{
      margin-left: auto
    }
}
table{
  *{
    color: #fff

  }
   input {
    width: 15px;
    height: 15px;
    margin: auto;
    margin-top: 0;
    margin-bottom: 5px;
  }
  p{
    text-align: center;
  }
  a{
    margin-left:15px;
    &:hover{
      i{
        transition: .5s;
        color: $min
      }
    }
  }
}
.pagination{
  margin-left: auto;
  margin-bottom: 0;
  margin-top: 35px;
  width: max-content;
  @media (max-width: 480px){
  font-size: 13px
}

}
tr{
 td{
   font-size: 16px;
   @media (max-width: 480px){
   font-size: 13px
 }
 }
}
