@font-face {
  font-family: "light";
  src: url("../fonts/OpenSans-Light.ttf") format("ttf"),
    url("../fonts/OpenSans-Light.woff") format("woff"),
    url("../fonts/OpenSans-Light.woff2") format("woff2"),
    url("../fonts/OpenSans-Light.eot") format("eot");
}

